<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <ctitle :title="'区域办理量动态'"></ctitle>
        <!--<span class="c-sxr">{{curName==='drop'?'全省':curName}}</span>-->
        <div class="w-echart" id="w-echart">
          <div ref="barEl">
            <horizontalBar :params="leftBarParams" @departmentClick="changeType"></horizontalBar>
          </div>
        </div>
      </div>
      <div class="w-right">
        <div class="c-top">
          <div class="part ct-left">
            <ctitle :title="'办理方式'"></ctitle>
            <div class="inner">
              <blfsList :data="blfsListData"></blfsList>
            </div>
          </div>
          <div class="part ct-right">
            <ctitle :title="'办理量走势'"></ctitle>
            <div class="inner">
              <timeChange class="timeChange" @change="zsTimeClick"></timeChange>
              <LinesR :params="zsLineParams"></LinesR>
            </div>
          </div>
        </div>
        <div class="c-bottom">
          <div class="part cb-left">
            <ctitle :title="'信访来源'"></ctitle>
            <div class="inner">
              <circleMoveChart :data="xflyData"></circleMoveChart>
            </div>
          </div>
          <div class="part cb-center">
            <ctitle :title="'投诉问题占比'"></ctitle>
            <div class="inner">
              <loopPie :params="tswtzbPieParams"></loopPie>
            </div>
          </div>
          <div class="part cb-right">
            <ctitle :title="'信访评价'"></ctitle>
            <div class="inner">
              <timeChange class="timeChange" @change="xfpjTimeClick"></timeChange>
              <LinesR :params="xfpjLineParams"></LinesR>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import timeChange from "@/components/timeChange/index";
  import ctitle from "@/components/title/index";
  import loopPie from "@/components/pie/LoopPie";
  import BScroll from "better-scroll";
  import horizontalBar from "@/components/bar/HorizontalBar";
  import LinesR from "@/components/line/LinesR";
  import blfsList from "@/components/list/blfsList";
  import circleMoveChart from "@/components/circleMoveChart/index";

  export default {
    name: "sxr",
    components: {
      ctitle,
      loopPie,
      horizontalBar,
      LinesR,
      timeChange,
      blfsList,
      circleMoveChart
    },
    data() {
      return {
        curName: 'drop',
        listScroll: null,
        // 区域受理量动态
        leftBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options:{
            click:true
          }
        },
        //办理方式
        blfsListData: [],
        // 信访量走势
        zsLineParams: {
          data: [],
          colors: [
            // 紫
            {
              line: "rgba(220, 150, 209, 1)",
              start: "rgba(220, 150, 209, 0.3)",
              end: "rgba(220, 150, 209, 0)"
            },
          ],
          option: {
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            axisYName: "件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "15%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
        },
        //投诉问题占比
        tswtzbPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        },
        // 信访来源
        xflyData:[],
        // 信访评价
        xfpjLineParams: {
          data: [],
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisYName:"%",
            axisCategoryName: "年",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "15%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "15%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {},
          xName: "name"
        }
      };
    },
    mounted() {
      this.getLeftAPI(this.year);
    },
    methods: {
      getLeftAPI(year) {
        // 1区域受理量动态（左侧柱图）
        get_http("jghxZrdwAqhLeftBar", {year: year})
          .then(res => {
            this.leftBarParams.data = res.data.data.sort((a, b) => a.value - b.value);
            if (this.leftBarParams.data.length > 14) {
              this.computation();
            }
            this.getRightAPI(year, this.curName);
          })
          .catch(err => {
            console.log(err);
          });
      },
      getRightAPI(year, name) {
        // 2办理方式
        get_http("jghxZrdwAqhBlfsList", {year: year, shij_02: name})
          .then(res => {
            this.blfsListData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3信访量走势
        this.getZsLineAPI(year, name, '年度');
        // 4信访来源
        get_http("jghxZrdwAqhXflyCircle", {year: year, shij_02: name})
          .then(res => {
            this.xflyData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5投诉问题占比
        get_http("jghxZrdwAqhTswtzbPie", {year: year, shij_02: name})
          .then(res => {
            this.tswtzbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 6信访评价
        this.getXfpjLineAPI(year, name, '年度');
      },
      // 3信访量走势
      getZsLineAPI(year, name, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        get_http("jghxZrdwAqhZsLine", {year: _year, shij_02: name, timetype: timetype})
          .then(res => {
            this.zsLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 6信访评价
      getXfpjLineAPI(year, name, time) {
        let timetype = '', _year = '';
        if (time === '年度') {
          timetype = 'cy';
          _year = '[now-4, now]'
        } else {
          timetype = 'cm';
          _year = year
        }
        get_http("jghxZrdwAqhXfpjLine", {year: _year, shij_02: name, timetype: timetype})
          .then(res => {
            this.xfpjLineParams.option.axisCategoryName = (time === '年度' ? '年' : '月');
            this.xfpjLineParams.map = res.data.map;
            this.xfpjLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 信访量走势-时间切换
      zsTimeClick(time) {
        this.getZsLineAPI(this.year, this.curName, time);
      },
      // 信访评价-时间切换
      xfpjTimeClick(time) {
        this.getXfpjLineAPI(this.year, this.curName, time);
      },
      // 点击左侧柱图
      changeType(name) {
        this.curName=(name === ''?'drop':name);
        this.getRightAPI(this.year, this.curName);
      },
      computation() {
        let height = (60 * this.leftBarParams.data.length) / 100 + "rem";
        $(this.$refs.barEl).css('height', height);
        this.initScroll();
      },
      // 初始化滚动条
      initScroll() {
        let listwraper = document.getElementById("w-echart");
        this.scrollFunc(this.listScroll, listwraper);

        // 更改滚动条样式
        $(".bscroll-vertical-scrollbar").css({
          width: ".1rem",
          background: "rgba(2, 144, 213, .2)"
        });
        $(".bscroll-vertical-scrollbar .bscroll-indicator").css({
          backgroundImage:
            "linear-gradient(to bottom, rgba(14, 149, 210, 0.8) , rgba(19, 240, 255, 0.8))",
          border: "none"
        });
        $(".bscroll-horizontal-scrollbar .bscroll-indicator").css({
          backgroundImage:
            "linear-gradient(to right, rgba(14, 149, 210, 0.8) , rgba(19, 240, 255, 0.8))",
          border: "none"
        });
      },
      scrollFunc(scrollName, scrollWrap) {
        if (!scrollName) {
          scrollName = new BScroll(scrollWrap, {
            bounce: false,
            momentumLimitDistance: 5,
            scrollX: true,
            scrollbar: {
              fade: false,
              interactive: false // 1.8.0 新增
            },
            mouseWheel: true
          });
        } else {
          // 复用Bscroll
          scrollName.refresh();
        }
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getLeftAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box

      .w-left
        width 3rem
        height 100%
        position relative
        font-size 0
        border-right 1px #91e3f7 dashed
        .c-sxr
          margin-left .4rem
          line-height .3rem
          font-size .16rem
          color #fff
          text-shadow 0 0 10px #276bf4
        .w-echart
          position relative
          width 100%
          height calc(100% - 0.28rem)
          overflow hidden
          > div
            height 100%
      .w-right
        display flex
        flex 1
        flex-direction column
        box-sizing border-box
        .part
          position relative
          flex 1
          width 100%
          height 100%
          .inner
            display flex
            height calc(100% - 0.28rem)
            justify-content center
            .chart
              width 100%
              height 100%
            .timeChange
              top 0
              right 1rem
        .c-top
          flex 1
          display flex
          position relative
          .ct-left
            flex 2
          .ct-right
            flex 1.5

        .c-bottom
          flex 1
          display flex
          .cb-left
            flex 1
          .cb-center
            flex 1
          .cb-right
            flex 1.5

</style>
